const DELAY = 1000;
const quantity = 100;

const processOrder = (value) => {
    return new Promise((resolve, reject) => {
        setTimeout(() => {
            if (Number.isInteger(+value)) {
                let ready = value <= quantity;
                ready ? resolve('Ð’Ð°Ñˆ Ð·Ð°ÐºÐ°Ð· Ð³Ð¾Ñ‚Ð¾Ð²!') : resolve('Ðš ÑÐ¾Ð¶Ð°Ð»ÐµÐ½Ð¸ÑŽ Ð½Ð° ÑÐºÐ»Ð°Ð´Ðµ Ð½ÐµÐ´Ð¾ÑÑ‚Ð°Ñ‚Ð¾Ñ‡Ð½Ð¾ Ñ‚Ð¾Ð²Ð°Ñ€Ð¾Ð²')
            } else {
                reject('ÐÐµÐºÐ¾Ñ€Ñ€ÐµÐºÑ‚Ð½Ñ‹Ð¹ Ð²Ð²Ð¾Ð´!');
            }
        }, DELAY)
    })
}

